}

html {
  font-size: 20px;
}

body {
  font-family: 'Montserrat', sans-serif;
  color: #333333;
  line-height: 1.6em;
  background: #efefef;
}

h1,
h2 {
  line-height: 1.2em;
}

img {
  max-width: 100%;
  max-height: auto;
}

.wrapper {
  width: 800px;
  margin: 0 auto;
  max-width: 100%;
}

#header {
  background: #7abbff;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

#menu {
  text-align: right;
}

#title h1 {
  margin: 0;
}

#title-area {
  display: grid;
  grid-template-columns: 2fr 3fr 5fr;
  padding-top: 20px;
  font-size: 0.9rem;
}

#title-area button {
  background: #fff;
  border: 0;
  border-radius: 3px;
  margin: 0 5px;
  padding: 5px 10px;
  cursor: pointer;
}

#paper {
  background: #fff;
  padding: 20px 50px;
  margin-bottom: 20px;
  min-height: 600px;
  box-shadow: 0 10px 10px #ddd;
}

.upload-btn-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-flex !important;
  cursor: pointer;
}

#input-button {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}

#input-button button {
  cursor: pointer;
}

.input-number {
  width: 50px;
}

#toolbar {
  padding-top: 20px;
  padding-bottom: 10px;
}

#toolbar button {
  background: none;
  border: 0;
  cursor: pointer;

  font-size: 14px;
  font-weight: normal;
}

#toolbar button:hover {
  color: #fff;
}

pre {
  display: block;
  background: #efefef;
  padding: 20px;
  margin: 0;
}

code {
  background: #efefef;
  padding: 3px 5px;
  border-radius: 5px;
}

blockquote {
  font-family: 'Charm', cursive;
  padding: 10px;
  line-height: 1.2em;
  font-size: 2.2rem;
  /* border-top: 1px solid #333;
  border-bottom: 1px solid #333; */
}

